body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
  background-color: #42455a;
}
.App{
  background-color:#19a0fa;
  min-height: 450px;
  padding-bottom: 20px;
  width: 400px;
  margin: auto;
  border-radius: 15px;
}
#to-do-form input{
  background-color: rgb(252, 123, 3);
  border: 0;
  width: 200px;
  height: 50px;
  padding: 0 20px;
  margin: 25px;
  font-size: 18px;
  border-radius: 15px;
  color: #fff;
}
#to-do-form input::-webkit-input-placeholder{
  color: rgba(255, 255, 255, 0.5);
}
#to-do-form input:-ms-input-placeholder{
  color: rgba(255, 255, 255, 0.5);
}
#to-do-form input::-ms-input-placeholder{
  color: rgba(255, 255, 255, 0.5);
}
#to-do-form input::placeholder{
  color: rgba(255, 255, 255, 0.5);
}
#to-do-form input:focus,#to-do-form input button:focus{
  outline: none;
}
#to-do-form button{
  height: 50px;
  width: 80px;
  border-radius: 15px;
  border: 0;
  background-color: rgb(74, 252, 3);
  cursor: pointer;
  font-weight: bolder;
  color: #fff;
}
.MyApp ul{
	display:inline-flex;
	margin:50px;
}
.MyApp ul li{
	list-style:none;
	margin:0px 20px;
}
.logo img{
	width:30px;
	margin-top:-7px;
  margin-right:48px;
  cursor:pointer; 
}

.list{
    color: #fff;
    width: 300px;
    background-color: #bf0707;
    height: 50px;
    margin: 20px auto;
    border-radius: 10px;
}
.list p{
    padding: 12px;
    position: relative;
}
.list p span{
    position: absolute;
    top: 15px;
    right: 15px;
    cursor: pointer;
}
.faicons{
    margin: 0 5px;
    font-size: 15px;
}
.list p input{
    background-color: transparent;
    border: 0px;
    color: #fff;
    font-size: 18px;
}
.list p input:focus{
    outline: none;
}
